<template>
	<div id="app">
		<div class="nav-title">
			<input type="text" placeholder="请输入..." v-model="context" @keyup.enter="addTaskHandle" />
			<ul class="nav-item">
				<router-link tag="li" to="/alltask">全部任务</router-link>
				<router-link tag="li" to="/completed">已完成</router-link>
				<router-link tag="li" to="/incomplete">未完成</router-link>
			</ul>
			<router-view />
			<div>剩余待办事项 {{ acountItem }} 个</div>
			<p>{{ $store.getters.isShow }}</p>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
	name: 'app',
	data() {
		return {
			context: '',
			oId: 1
		};
	},
	methods: {
		addTaskHandle(e) {
			var data = JSON.parse(localStorage.getItem('dataLists') || '[]');

			data.forEach(item => {
				if (item.id === data.length) {
					this.oId = item.id + 1;
				}
			});
			var list = { id: this.oId++, text: this.context, isShow: false };

			this.$store.commit('add', list);
			this.context = '';
		}
	},
	computed: {
		...mapGetters(['acountItem'])
	}
};
</script>

<style scoped>
#app {
	width: 100%;
	height: 100%;
}
.nav-title {
	display: flex;
	flex-direction: column;
	width: 50%;
	margin: auto;
}
.nav-title input {
	height: 40px;
	line-height: 40px;
	font-size: 20px;
	padding: 0 5px;
}
.nav-item {
	display: flex;
	list-style: none;
	margin: 0;
	padding: 0;
}
.nav-item li {
	flex: 1;
	text-align: center;
	padding: 10px 0;
}
.router-link-active {
	border-bottom: 3px solid #ff0000;
}
</style>
