import vue from 'vue'
import VueRouter from 'vue-router'

vue.use(VueRouter)

var router = new VueRouter({
	mode: "history",
	routes: [
		{
			path: '/alltask',
			name: 'alltask',
			component: () => import('@/components/AllTask')
		},
		{
			path: '/completed',
			name: 'completed',
			component: () => import('@/components/Completed')
		},
		{
			path: '/incomplete',
			name: 'incomplete',
			component: () => import('@/components/Incomplete')
		},
		{
			path: '/',
			redirect: 'alltask'
		}
	]
})

export default router;