import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

var store = new Vuex.Store({
	state: {
		dataList: JSON.parse(localStorage.getItem('dataLists') || "[]")
	},
	mutations: {
		add (state, payload) {
			state.dataList.push(payload)
			localStorage.setItem("dataLists", JSON.stringify(state.dataList))
		},
		// 修选中状态
		modifySelected (state, obj) {
			state.dataList.some(item => {
				if (item.id == obj.id) {
					item.isShow = !item.isShow;
					return true;
				}
			})
			localStorage.setItem("dataLists", JSON.stringify(state.dataList))
		}
	},
	getters: {
		completed (state) {
			return state.dataList.filter(item => item.isShow === true)
		},
		incomplete (state) {
			return state.dataList.filter(item => item.isShow === false)
		},
		acountItem (state) {
			return state.dataList.filter(item => item.isShow === false).length
		},
		// 是否被选中
		isShow (state) {
			var o = {};
			state.dataList.forEach(item => {
				o[item.id] = item.isShow;
			})
			return o;
		}
	}
})

export default store;